import React, { useState } from 'react';
import { Line } from 'react-chartjs-2';

function LineGraphInput() {
  const [input, setInput] = useState('');
  const [dataPoints, setDataPoints] = useState([]);
  const [yMax, setYMax] = useState(100);
  const [yMaxInput, setYMaxInput] = useState('100');

  // 
  const [editIndex, setEditIndex] = useState(null);
  const [editValue, setEditValue] = useState('');

  // 
  const handleChange = (e) => setInput(e.target.value);

  // 
  const handleYMaxChange = (e) => setYMaxInput(e.target.value);

  // 
  const applyYMax = () => {
    const num = Number(yMaxInput);
    if (!isNaN(num) && num > 0) {
      setYMax(num);
    }
  };

  // 14
  const handleAdd = () => {
    const num = Number(input);
    if (!isNaN(num)) {
      let newData = [...dataPoints, num];
      if (newData.length > 12) {
        newData = newData.slice(newData.length - 12); // 12
      }
      setDataPoints(newData);
      setInput('');
    }
  };

  // 
  const startEdit = (index) => {
    setEditIndex(index);
    setEditValue(dataPoints[index].toString());
  };

  // 
  const handleEditChange = (e) => {
    setEditValue(e.target.value);
  };

  // 
  const saveEdit = () => {
    const num = Number(editValue);
    if (!isNaN(num)) {
      const newData = [...dataPoints];
      newData[editIndex] = num;
      setDataPoints(newData);
      setEditIndex(null);
      setEditValue('');
    }
  };

  // 
  const cancelEdit = () => {
    setEditIndex(null);
    setEditValue('');
  };

  // 12
  const labels = Array.from({ length: 12 }, (_, i) => (i + 1).toString());

  // 12null
  const displayData =
    dataPoints.length < 12
      ? Array(12 - dataPoints.length).fill(null).concat(dataPoints)
      : dataPoints.length > 12
      ? dataPoints.slice(dataPoints.length - 12)
      : dataPoints;

  const data = {
    labels,
    datasets: [
      {
        label: '',
        data: displayData,
        fill: false,
        borderColor: 'blue',
        spanGaps: true,
      },
    ],
  };

  const options = {
    scales: {
      y: {
        min: 0,
        max: yMax,
      },
    },
  };

  return (
    <div style={{ padding: 20 }}>
      {/*  */}
      <div style={{ marginBottom: 10 }}>
        <input
          type="number"
          value={input}
          onChange={handleChange}
          placeholder=""
          style={{ marginRight: 10 }}
        />
        <button onClick={handleAdd}></button>
      </div>

      {/*  */}
      <div style={{ marginBottom: 20 }}>
        <label>
          
          <input
            type="number"
            value={yMaxInput}
            onChange={handleYMaxChange}
            style={{ width: 80, marginLeft: 8, marginRight: 8 }}
          />
        </label>
        <button onClick={applyYMax}></button>
      </div>

      {/*  */}
      <div style={{ maxWidth: 600 }}>
        <Line data={data} options={options} />
      </div>

      {/*  */}
      <div style={{ marginTop: 20, maxWidth: 600 }}>
        <h3></h3>
        <table style={{ width: '100%', borderCollapse: 'collapse' }}>
          <thead>
            <tr>
              <th style={{ border: '1px solid #ccc', padding: 8 }}></th>
              <th style={{ border: '1px solid #ccc', padding: 8 }}></th>
              <th style={{ border: '1px solid #ccc', padding: 8 }}></th>
            </tr>
          </thead>
          <tbody>
            {dataPoints.map((val, idx) => (
              <tr key={idx}>
                <td style={{ border: '1px solid #ccc', padding: 8 }}>{idx + 1}</td>
                <td style={{ border: '1px solid #ccc', padding: 8, cursor: 'pointer' }}>
                  {editIndex === idx ? (
                    <input
                      type="number"
                      value={editValue}
                      onChange={handleEditChange}
                      style={{ width: 80 }}
                    />
                  ) : (
                    <span onClick={() => startEdit(idx)}>{val}</span>
                  )}
                </td>
                <td style={{ border: '1px solid #ccc', padding: 8 }}>
                  {editIndex === idx ? (
                    <>
                      <button onClick={saveEdit} style={{ marginRight: 8 }}>
                        
                      </button>
                      <button onClick={cancelEdit}></button>
                    </>
                  ) : (
                    <button onClick={() => startEdit(idx)}></button>
                  )}
                </td>
              </tr>
            ))}
            {dataPoints.length === 0 && (
              <tr>
                <td colSpan={3} style={{ padding: 8, textAlign: 'center' }}>
                  
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default LineGraphInput;